.tab {
  position: fixed;
  bottom: 0;
  height: 0.98rem;
  width: 100%;
  background: white;
  display: flex;
  z-index: 10;
}
.tab > .tabItem {
  flex: 1;
}
.tab > .tabItem.active {
  font-weight: bolder;
}
.tab > .tabItem.active .itemName {
  color: #DD1A21;
}
.tab > .tabItem.active .itemIcon {
  color: #DD1A21;
}
.itemIcon {
  text-align: center;
  font-size: 0.44rem;
  transition: all 0.5s ease-in-out;
  color: #bbb;
}
.itemIcon i {
  font-size: 0.52rem;
}
.itemName {
  text-align: center;
  font-size: 0.28rem;
  line-height: 0.28rem;
  transition: all 0.5s;
  margin-top: 0.1rem;
  color: #bbb;
}
.noSelect {
  user-select: none;
}
